import React, { useEffect, useState } from "react";
import { Formik, ErrorMessage, Field } from "formik";
import * as Yup from "yup";
import {
  Autocomplete,
  Box,
  Button,
  Card,
  Checkbox,
  FormControl,
  FormControlLabel,
  TextField,
  makeStyles,
} from "@material-ui/core";
import Header_Design from "../Transport/Header_Design";
import { customAxios } from "../apis/Axios";
const useStyles = makeStyles((theme) => ({
  root: {
    "& .MuiInputBase-input": {
      height: "auto",
      minHeight: "100px",
      padding: theme.spacing(2),
    },
    "& .MuiInputBase-multiline": {
      lineHeight: 1.2,
    },
  },
}));
const ComplainType = [
  { label: "Academic Issues", value: 1 },
  { label: "Behavior Issues", value: 2 },
  { label: " Harassment or Bullying", value: 3 },
  { label: "Safety Issues", value: 4 },
];


const validationSchema = Yup.object({
  // names: Yup.string().required("Required"),
  // ComplaintNo: Yup.string().ComplaintNo("Invalid ComplaintNo").required("Required"),
  // date: Yup.date().required("Required"),
  // category: Yup.string().required("Required"),
  // description: Yup.string().required("Required"),
});

const ComplaintForm = () => {
  const classes = useStyles();
  const [selectedOptions, setSelectedOptions] = useState([]);
  const [options, setOptions] = useState([]);
  const handleOptionChange = (event, values) => {
    setSelectedOptions(values);
  };
  const handleSelectAll = () => {
    setSelectedOptions(options);
  };


  const data = selectedOptions.map((ele) => ele.id);
  console.log(data, selectedOptions);
  const handleClearAll = () => {
    setSelectedOptions([]);
  };
  useEffect(() => {
    const fetchOptions = async () => {
      const response = await customAxios.getCall("list/search");

      setOptions(response);
    };
    fetchOptions();
  }, []);

  return (
    <div>
      <h1>Complaint Form</h1>
      <Formik
        initialValues={{
          ComplaintAgaint: "",
          ComplaintNo: "",
          names: "",
          date: "",
          category: "",
          description: "",
          Complaintby: "",
          photo: null,
          confidentiality: false,
        }}
        validationSchema={validationSchema}
        onSubmit={async (values) => {
          console.log(values);
          let idString = "";
          selectedOptions.forEach((ele) => {
            idString = ele.id + "," + idString;
          });
          // console.log(idString);

          idString = idString.slice(0, -1);
          // console.log(idString);
          const object = {
            id: idString,
          };

          // return;
          const formData = new FormData();

          formData.append("ComplaintNo", values.ComplaintNo);
          formData.append("names", values.names);
          formData.append("date", values.date);
          formData.append("category", values.category);
          formData.append("description", values.description);
          formData.append("Complaintby", values.Complaintby);
          formData.append("ComplaintAgaint", JSON.stringify(object));
          formData.append("photo", values.photo);
          formData.append("confidentiality", values.confidentiality);
          console.log(formData);
          try {
            // for (const id of data) {
              const res = await customAxios.postFormData(
                "complains/complaits",
                formData
                );
                console.log("res", res);
                console.log(id, object);
            // }
          } catch (error) {
            console.log(error);
          }

          // console.log(values, selectedOptions);
        }}
      >
        {({
          isSubmitting,
          handleChange,
          handleSubmit,
          values,
          errors,
          touched,
          setFieldValue,
        }) => (
          <>
            <Box>
              <Card style={{ padding: 16 }}>
                <form onSubmit={handleSubmit}>
                  <Box sx={{ mt: 2 }}>
                    <TextField
                      required
                      id="names"
                      name="names"
                      label="Name"
                      fullWidth
                      value={values.names}
                      onChange={handleChange}
                      error={Boolean(touched.names && errors.names)}
                      // fullWidth
                      helperText={touched.names && errors.names}
                    />
                  </Box>
                  <Box sx={{ mt: 2 }}>
                    <TextField
                      required
                      id="ComplaintNo"
                      name="ComplaintNo"
                      label="Complaint No"
                      fullWidth
                      value={values.ComplaintNo}
                      onChange={handleChange}
                    />
                  </Box>
                  <Box sx={{ mt: 2 }}>
                    <TextField
                      required
                      id="Complaintby"
                      name="Complaintby"
                      label="Complaint By"
                      fullWidth
                      value={values.Complaintby}
                      onChange={handleChange}
                    />
                  </Box>
                  <Box
                    style={{
                      display: "flex",
                      alignItems: "start",
                      flexDirection: "column",
                    }}
                  >
                    <Box style={{ flexDirection: "row", marginLeft: 8 }}>
                      <FormControlLabel
                        control={
                          <Checkbox
                            checked={selectedOptions.length === options.length}
                            onChange={handleSelectAll}
                          />
                        }
                        label="Select All"
                      />
                      <FormControlLabel
                        control={
                          <Checkbox
                            checked={selectedOptions.length === 0}
                            onChange={handleClearAll}
                          />
                        }
                        label="Clear All"
                      />
                    </Box>
                    <Autocomplete
                      id="ComplaintAgaint"
                      style={{ margin: 4, width: "95%" }}
                      multiple
                      getOptionLabel={(option) => option.name}
                      options={options}
                      value={selectedOptions}
                      onChange={handleOptionChange}
                      renderInput={(params) => (
                        <TextField
                          fullWidth
                          label="Complaint Againt"
                          name="ComplaintAgaint"
                          // onChange={handleChange}
                          variant="outlined"
                          {...params}
                        />
                      )}
                    />
                  </Box>
                  <Box sx={{ display: "flex", flexWrap: "wrap" }}>
                    <Box sx={{ mt: 2, width: "25ch", marginRight: 5 }}>
                      <TextField
                        id="date"
                        name="date"
                        label="Date"
                        value={values.date}
                        type="datetime-local"
                        defaultValue="2013-05-24T10:30"
                        fullWidth
                        onChange={handleChange}
                        InputLabelProps={{
                          shrink: true,
                        }}
                      />
                    </Box>
                    <FormControl sx={{ mt: 2 }}>
                      <TextField
                        // fullWidth
                        labelId="category"
                        select
                        variant="outlined"
                        SelectProps={{ native: true }}
                        value={values.category}
                        onFocus={handleChange}
                        onChange={handleChange}
                        id="category"
                        label="Category"
                        name="category"
                      >
                        {ComplainType.map((option) => (
                          <option key={option.value} value={option.value}>
                            {option.label}
                          </option>
                        ))}
                      </TextField>
                    </FormControl>
                  </Box>
                  <Box sx={{ mt: 2 }}>
                    <TextField
                      name="description"
                      label="Message"
                      variant="outlined"
                      multiline
                      rows={4}
                      value={values.description}
                      onChange={handleChange}
                      fullWidth
                      className={classes.root}
                    />
                    <ErrorMessage name="description" />
                  </Box>
                  <Box sx={{ mt: 2 }}>
                    <div>
                      <label htmlFor="photo">Choose a file:</label>
                      <input
                        id="photo"
                        name="photo"
                        type="file"
                        onChange={(event) => {
                          setFieldValue("photo", event.currentTarget.files[0]);
                        }}
                      />
                      <ErrorMessage name="photo" />
                    </div>
                  </Box>

                  <label htmlFor="confidentiality">
                    Keep complaint confidential:
                  </label>
                  <Field type="checkbox" name="confidentiality" />
                  <ErrorMessage name="confidentiality" />

                  <Button
                    variant="contained"
                    type="submit"
                    disabled={isSubmitting}
                  >
                    Submit
                  </Button>
                </form>
              </Card>
            </Box>
          </>
        )}
      </Formik>
    </div>
  );
};

export default ComplaintForm;
